import './text.css';
import './App.css';
import PropTypes from "prop-types";
import {Button, Box, Tabs, Tab, Typography} from '@mui/material'
import MyWheel from './wheel'
import {useRef, useState} from "react";
import Confetti from "react-confetti";
import {useWindowSize} from "react-use";
import Clock from 'react-live-clock'

function TabPanel(props){
    const {children, value, index, ...other} = props;

    return (
        <div
            role={"tabpanel"}
            hidden={value !== index}
            id={`simple-tabpanel-${index}`}
            aria-labelledby={`simple-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box className={"text-white"} sx={{p:3}}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    )
};

TabPanel.propTypes={
    children: PropTypes.node,
    index: PropTypes.number.isRequired,
    value: PropTypes.number.isRequired
};

function allyProps(index){
    return{
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`,
    };
}


function App() {
    let [helloWorld, setHelloWorld] = useState(false);
    const [value, setValue] = useState(0);
    const handleChange = (event,newValue) => {
        setValue(newValue);
    }

    const helloBtn = () => {
        console.log(helloWorld)
        setHelloWorld(true)
        if (helloWorld===true){
            setHelloWorld(false)
        }
    }

    //룰렛 관련
    const {width, height} = useWindowSize();

    const [inputs, setInputs] = useState({option: ''})
    const {option} = inputs;
    const onChange = e => {
        const {name, value} = e.target;
        setInputs({
            ...inputs,
            [name]: value
        })
    }
    const [data, setData] = useState([
        { id:1, option: '컵밥'},
        { id:2, option: '덮밥'},
    ]);
    const nextId = useRef(4);


    const onCreate = () => {
        const food = {
            id: nextId.current,
            option
        }
        if(food.option===''){
            alert('음식명을 입력해주세요!');
            return;
        }
        setData([...data, food])
        setInputs({option: ''})

        nextId.current +=1;
    }

    //제거
    const onRemove = id => {
        setData(data.filter(food => food.id !== id));
    }
  return (
    <div className="App">
        {helloWorld===true ? <Confetti width={width} height={height} /> : <></>}
        <div style={{display:"flex", justifyContent:"space-between"}}>
            <div></div>
            <span style={{marginLeft:"18%"}} className="text-white tb-3">Roulette</span>
            <div className={'tb-2'}>
                <Clock className={'text-white'} format='YYYY/MM/DD HH:mm:ss' ticking={true} timezone={'Asia/Seoul'} />
            </div>
        </div>
        <Box sx={{width:'100%', color:'white'}}>
        <Box sx={{border: 1, borderColor: 'primary', color:'white'}}>
            <Tabs centered value={value} onChange={handleChange} aria-labelledby={"basic tabs example"}>
                <Tab style={{backgroundColor:"#e5e4f5"}} label="Item1" {...allyProps(0)}/>
                <Tab style={{backgroundColor:"#e5e4f5"}} label="Item2" {...allyProps(1)}/>
                <Tab style={{backgroundColor:"#e5e4f5"}} label="Item3" {...allyProps(2)}/>
            </Tabs>
        </Box>
            <TabPanel className={"text-white"} value={value} index={0}>
                <div>
                    <Button variant="contained" onClick={helloBtn} color="primary">Hello World</Button>
                </div>
                <div>
                    <MyWheel data={data} option={option} onCreate={onCreate} onChange={onChange} onRemove={onRemove}/>
                </div>
            </TabPanel>
            <TabPanel value={value} index={1}>
                Item2
            </TabPanel>
            <TabPanel value={value} index={2}>
                Item3
            </TabPanel>
        </Box>

    </div>
  );
}

export default App;
